export const IS_LOGGING = 'IS_LOGGING';
export const SET_USERINFO = 'SET_USERINFO';
export const IS_DURING_MEETING= "IS_DURING_MEETING";
export const SET_CURRENT_VIEW = "SET_MEETING_VIEW";
export const SET_SHOW_NOTIFY_USER_INSTALL = "SET_SHOW_NOTIFY_USER_INSTALL";

export function setIsDuringMeeting(flag) {
  return {
      type: IS_DURING_MEETING,
      payload: flag,
  }
}

export function setUserInfo(info) {
    return {
        type: SET_USERINFO,
        payload: info
    }
}

export function setCurrentView(name) {
  return {
    type: SET_CURRENT_VIEW,
    payload: name
  }
}

export function setNotifyUserInstall(show) {
  return{
    type: SET_SHOW_NOTIFY_USER_INSTALL,
    payload: show
  }
}