import React, { useState, useEffect } from 'react';
import './App.scss';
import { connect } from 'react-redux';
import JoinMeeting from "./JoinMeeting";
import Home from './Home';
import { isInStandaloneMode } from '../utils'
import { usePrevious } from '../hooks/usePrevious'
import {
  MeetingViews
} from '../store/reducer';
import { 
  getUserInfo
} from "../store/thunk-actions";

function App(props) {
    let {
      meetingCurrentView,
      userInfo,
      dispatch
    } = props;
    let [notifyVisible, setNotifyVisible] = useState(false);
    // let previousUserInfo = usePrevious(userInfo);

    useEffect( () => {
      if(!isInStandaloneMode()) {
        if(!sessionStorage.getItem('noNotify')) {
          setNotifyVisible(true);
        }
      } else {
        setNotifyVisible(false);
      }
    }, []);

    useEffect( () => {
      console.log(userInfo);
      if(!userInfo) {
        dispatch( getUserInfo())
      }
    }, []);
    
    const onCloseNotifyInstall = () => {
      sessionStorage.setItem('noNotify', true);
      setNotifyVisible(false);
    }
    return (
        <>
            <div className="app">
              { meetingCurrentView === MeetingViews.Join && <JoinMeeting /> }
              { meetingCurrentView === MeetingViews.Home && <Home /> }
            </div>
            {
              notifyVisible && (
              <div className="app-notify-install">
                <span>Hi, you can install this page from the browser address bar.</span>
                <span className="app-notify-install-close" onClick={onCloseNotifyInstall}>X</span>
              </div>)
            }
        </>
    );
}

const mapStateToProps = ({meetingCurrentView, userInfo}) => {
  return {
    meetingCurrentView,
    userInfo
  }
}

export default connect(
  mapStateToProps,
)(App);

